.formBody {
    margin-bottom: 15px;
}

.formBtns {
    display: flex;
    justify-content: space-between;

    .formBtn {
        padding: 5px 10px;
        color: #FFFFFF;
        background-color: #000000;
        cursor: pointer;
        border: none;
        border-radius: 5px;

        &:hover {
            background-color: rgb(130, 130, 130);
        }

        &:disabled {
            background-color: rgb(206, 206, 206);
            cursor: default;
        }
    }
}